.buttonContainer {
    display: flex;
    gap: 24px;
    margin-bottom: 32px;
}

.all {
    --color: #06c;
}

.low {
    --color: #0c2;
}

.high {
    --color: #c00;
}

.middle {
    --color: #cc9c00;
}

.button {
    padding: 5px 15px;

    background: transparent;
    color: var(--color);

    border: 1px solid var(--color);
    border-radius: 15px;
    outline: none;

		font: 600 0.875rem/1.4rem Montserrat, sans-serif;
    cursor: pointer;
}

.button:active {
    background: var(--color);
    color: white;
}

.active {
    background: var(--color);
    color: white;
}

.affairs {
    display: flex;
    gap: 17px;
    flex-wrap: wrap;
    max-width: 450px;
}
